<template>
    <div class="shopcar">
        <van-nav-bar 
        title=""
        left-text=""
        left-arrow
        @click-left="back"
        >
            <template #right>
                <div @click="toggleEditStatus" v-if="shopbagData.length>0">{{isEdit?'':''}}</div>
            </template>
        </van-nav-bar>
        <div v-if="shopbagData.length > 0">
            <div class="shopbag-bg"></div>
            <div class="shopbag-box">
                <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text=""
                    offset="100"
                    @load="loadShopbagData"
                    >
                    <van-swipe-cell class="shopbag-cell" :disabled="isEdit" v-for="(item,index) in shopbagData" :key="item.sid" >
                        <div class="shopbag-item">
                            <div class="check">
                                <van-checkbox @change="simpleSelect" v-model="item.isChecked" icon-size="24px"></van-checkbox>
                            </div>
                            <div class="pro-info">
                                <div class="pro-img">
                                    <img class="auto-img" :src="item.small_img">
                                </div>
                                <div class="pro-text">
                                    <div class="text-box">
                                        <div class="pro-names">
                                            <div class="pro-name">{{item.name}}</div>
                                            <div class="pro-rule">{{item.rule}}</div>
                                        </div>
                                    <div class="pro-enname">{{item.enname}}</div>
                                    </div>
                                    <div class="count-box">
                                        <div class="pro-price">&yen;{{item.price}}</div>
                                        <div class="pro-count">
                                            <van-stepper  @change="updateShopbagCount(item.sid,item.count)" v-model="item.count"  theme="round" button-size="22" disable-input />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <template #right>
                            <van-button class="shopbag-remove-btn" square type="danger" text="" @click="removeShopbag([item.sid],index)" />
                        </template>
                    </van-swipe-cell>
                </van-list>
            </div>
            <van-submit-bar v-show="!isEdit" :price="total" button-text="" button-color="#0c34ba" @submit="buy">
                <van-checkbox v-model="checked" @click="allSelect"></van-checkbox>
            </van-submit-bar>
            <van-submit-bar v-show="isEdit" button-text="" button-color="#0c34ba" @submit="removeSelect">
                <van-checkbox v-model="checked" @click="allSelect"></van-checkbox>
            </van-submit-bar>
        </div>
        <div v-else>
            <van-empty description="" >
                <div class="guang" @click="goHome"></div>
            </van-empty>
        </div>
    </div>
</template>

<script>
    import '../assets/less/shopcar.less'
    import {mapMutations,mapState} from "vuex"
    export default {
        name:'Shopcar',
        data() {
            return {
                //
                isEdit:false,
                //
                checked:false,
                //
                shopbagData:[],
                //
                shopbagAllData:[],
                //  
                loading:true,
                //
                finished:false,
                //
                start:0,
                //
                count:5,
                //
                total:0
            }
        },
        created(){
            this.getShopbagData();
        },
        methods:{
            ...mapMutations(["changeShopbagCount"]),
            //
            goHome(){
                this.$router.push({name:"Home"});
            },
            //
            back(){
                if(this.shopbagData.length){
                    this.$router.go(-1);
                }else {
                    this.$router.push({name:"Home"});
                }
            },
            //
            toggleEditStatus(){
                this.isEdit = !this.isEdit;
            },
            //
            getShopbagData(){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //
                this.$toast.loading({
                    message: '...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'GET',
                    url:this.baseUrl + '/findAllShopcart',
                    params:{
                        appkey:this.appkey,
                        tokenString
                    }
                }).then( res => {
                    //
                    this.$toast.clear();
                    //tokenString
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString
                    if(res.data.code == 5000){
                        if(res.data.result.length == 0) {
                            return ;
                        }
                        res.data.result.forEach(v => {
                            //
                            v.isChecked = false;
                        });
                        this.shopbagAllData = res.data.result;
                        this.shopbagData = this.shopbagAllData.slice(this.start,this.start+this.count);
                        this.start += this.count;
                        this.loading = false; 
                        //
                        if(this.shopbagAllData.length < this.count){
                            this.finished = true;
                        }
                    }
                }).catch(err => {
                    //
                    //
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //
            loadShopbagData() {
                let data = this.shopbagAllData.slice(this.start,this.start+this.count);
                this.shopbagData.push(...data);
                this.start += this.count;
                // this.loading = false;
                if(data.length > 0) {
                    this.checked = false;
                }
                //
                if(data.length < this.count){
                    this.finished = true;
                    return ;
                }
            },
            //
            updateShopbagCount(sid,count) {
                let tokenString = this.$cookies.get("tokenString");
                //tokenString
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }
                //
                this.$toast.loading({
                    message: '...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/modifyShopcartCount',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        sid,
                        count
                    }
                }).then( res => {
                    //
                    this.$toast.clear();
                    //tokenString
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString
                    if(res.data.code == 6000){
                        this.sum();   
                    }
                }).catch(err => {
                    //
                    //
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //
            allSelect(){
                this.shopbagData.forEach( v => {
                    v.isChecked = this.checked;
                });
                this.sum();
            },
            //
            simpleSelect() {
                this.sum();
                //
                for(let i = 0;i < this.shopbagData.length;i++){
                    if(!this.shopbagData[i].isChecked) {
                        this.checked = false;
                        return;
                    }
                }
                this.checked = true;
            },
            //
            removeShopbag(sids,index){
                let tokenString = this.$cookies.get("tokenString");
                //tokenString
                if(!tokenString){
                    return this.$router.push({name:'Login'});
                }            
                //
                this.$toast.loading({
                    message: '...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/deleteShopcart',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        sids:JSON.stringify(sids)
                    }
                }).then( res => {
                    //
                    this.$toast.clear();
                    //tokenString
                    if(res.data.code == 700) {
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:3000
                        });
                        //
                        return this.$router.push({name:'Login'});
                    }
                    //tokenString
                    if(res.data.code == 7000){
                        //
                        if(index != undefined){
                            this.shopbagData.splice(index,1);
                        }else {
                            //
                            for(let i = 0;i < this.shopbagData.length;i++){
                                if(this.shopbagData[i].isChecked){
                                    this.shopbagData.splice(i,1);
                                    i--;
                                }
                            }
                        }
                        //
                        this.changeShopbagCount(this.shopbagCount - sids.length);
                        //
                        this.sum();
                        this.$nextTick(() => {
                            let cells = document.querySelectorAll(".shopbag-cell");
                            if(cells.length == 0 && this.shopbagData.length < this.shopbagAllData.length){
                                this.loadShopbagData();
                            }else {
                                let cellSize = cells[cells.length - 1].getBoundingClientRect();
                                let distance = cellSize.top;
                                let cellHeight = cellSize.height;
                                let innerHeight = window.innerHeight;
                                if(distance + cellHeight - innerHeight <= 50) {
                                    this.loadShopbagData();
                                }
                            }
                        })
                        this.$toast({
                            message: res.data.msg,
                            forbidClick: true,
                            duration:500
                        });
                        //
                        for(let i = 0;i < this.shopbagData.length;i++){
                            if(!this.shopbagData[i].isChecked){
                                return ;
                            }
                        }
                        this.checked = true;
                    }
                }).catch(err => {
                    //
                    //
                    this.$toast.clear();
                    this.$toast({
                        msg:err,
                        forbidClick:true,
                        duration:1000
                    });
                });
            },
            //
            removeSelect(){
                //sid
                let sids = [];
                //
                this.shopbagData.forEach( v => {
                    if(v.isChecked){
                        sids.push(v.sid);
                    }
                })
                //
                if(sids.length == 0){
                    this.$toast({
                        message:'',
                        forbidClick:true,
                        duration:3000
                    })
                    return;
                }
                this.removeShopbag(sids);
            },
            //
            sum(){
                let total = 0;
                this.shopbagData.forEach(v => {
                    if(v.isChecked) {
                        total += v.price * v.count ;
                    }
                });
                this.total = total * 100;
            },
            //
            buy(){
                //0,
                let sids = [];
                for(let j = 0;j < this.shopbagData.length;j++){
                    if(this.shopbagData[j].isChecked){
                        sids.push(this.shopbagData[j].sid);
                    }
                }
                if(sids.length == 0){
                    this.$toast({
                        message:"",
                        duration:1000,
                        forbidClick:true
                    })
                    return;
                }
                //
                this.$router.push({name:'Pay',params:{sids}});
            }
        },
        computed:{
            ...mapState(['shopbagCount'])
        }
    }
</script>

